	data() {
		return {
			selectAnchorLeft: 'on',
			selectAnchorRIght: ''
		};
	},
	methods: {
		toAnchorLeft() {
			this.selectAnchorRIght = '';
			this.selectAnchorLeft = 'on';
		},
		toAnchorRight() {
			this.selectAnchorLeft = '';
			this.selectAnchorRIght = 'on';
		}
	}
};
